<template>
    <div class="all">
        <el-button @click="random">随机一个</el-button>
        <el-carousel :autoplay="false" type="card" height="350px" ref="carouselOly" indicator-position="none" @change="carouselChange" class="carousel" @click.native="selected">
            <el-carousel-item v-for="item in dataset" :key="item.Year + item.Season">
                <el-card class="box-card" shadow="always">
                    <div slot="header" class="clearfix">
                        <span> {{ `${item.Year}年${item.主办城市}奥运会` }} </span>
                    </div>
                    <el-image :src="item.imgUrl">
                        <div slot="error" class="image-slot">
                            <fiveRound :name="`${item.Year}年${item.主办城市}奥运会`" style="width:141px;height:141px" />
                        </div>
                    </el-image>
                    <div>点击查看详情</div>
                </el-card>
            </el-carousel-item>
        </el-carousel>
        <div class="tips">左右滑动可选择赛事哦！</div>
        <swiper ref="swiper" :options="swiperOption" class="swiper" @touchEnd="transitionStart">
            <!-- slides -->

            <swiper-slide v-for="(item, index) in dataset" :key="'步骤条-' + item.Year + item.Season">
                <div :class="{ text: true, Summer: item.Season == 'Summer', Winter: item.Season == 'Winter' }">
                    {{ (item.Season == "Summer" ? "夏季 " : "冬季 ") + `${item.Year}年${item.主办城市}奥运会` }}
                </div>
            </swiper-slide>
        </swiper>

        <el-dialog
            class="cursive"
            :title="`${dataset[select].Year}年${dataset[select].主办城市}奥运会`"
            :visible.sync="dialogVisible"
            width="70%"
            :modal="false"
            custom-class="animate__animated animate__bounceIn"
            :close-on-click-modal="false"
        >
            <div class=" description">
                {{ dataset[select].届数 }}
                {{ dataset[select].主办城市 }}
                {{ dataset[select].举办国家 }}
                {{ dataset[select].举办时间 }}
                {{ dataset[select].口号 }}

                {{ dataset[select].Year >= 1994 ? (dataset[select].Season == "Summer" ? "夏季奥运会" : "冬季奥运会") : "" }}
            </div>

            <el-row>
                <el-col :span="16">
                    <p class=" description">{{ dataset[select].text || "" }}</p>
                    <el-link class="" type="success" style="float: right" :href="'https://baike.baidu.com/item/' + dataset[select].赛事名称">
                        ——来自百度百科
                        <i class="el-icon-link"></i>
                    </el-link>
                </el-col>
                <el-col :span="8" class="centerBox">
                    <el-image :src="dataset[select].imgUrl">
                        <div slot="error" class="image-slot">
                            <fiveRound :name="dataset[select].赛事名称" style="width:141px;height:141px" />
                        </div>
                    </el-image>
                </el-col>
            </el-row>
            <div class="tips">点击图例可以切换显示状态哦</div>
            <el-radio-group v-model="radio">
                <el-radio-button label="金牌"></el-radio-button>
                <el-radio-button label="银牌"></el-radio-button>
                <el-radio-button label="铜牌"></el-radio-button>
            </el-radio-group>

            <router-view class="animate__animated "></router-view>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import meinian from "@/components/每年奖牌趋势.vue";
    import fiveRound from "./fiveRound.vue";
    export default {
        components: {
            fiveRound,
            meinian,
        },
        data() {
            return {
                swiperOption: {
                    slidesPerView: 21, // 显示数量
                    freeMode: true,
                    height: 400,
                    centeredSlides: true,
                },
                radio: "金牌",
                Medal: "Gold",
                dataset: [{}],
                select: 0,
                dialogVisible: false,
                active: 1,
            };
        },
        async mounted() {
            this.dataset = await fetch("./奥运会赛事统一数据最终添加赛事介绍.json", { cache: "force-cache" }).then((res) => res.json());
            this.random();
        },
        methods: {
            carouselChange(index) {
                this.$refs.swiper.$swiper.slideTo(index, 1000, false);
            },
            random() {
                this.$refs.carouselOly.setActiveItem(Math.floor(Math.random() * (this.dataset.length + 1)));
            },
            selected() {
                this.select = this.$refs.carouselOly.activeIndex;
                let { Season, Year, Medal } = this.$route.query;
                if (Season != this.dataset[this.select].Season || this.dataset[this.select].Year != Year || this.Medal != Medal) {
                    this.$router.push({
                        query: {
                            Season: this.dataset[this.select].Season,
                            Year: this.dataset[this.select].Year,
                            Medal: this.Medal,
                            opened: true,
                        },
                    });
                }
                this.dialogVisible = true;
            },
            transitionStart(i) {
                console.log(i.activeIndex);
                this.$refs.carouselOly.setActiveItem(i.activeIndex);
            },
        },
        props: [],
        watch: {
            radio(Medal) {
                switch (Medal) {
                    case "金牌":
                        this.Medal = "Gold";
                        break;
                    case "银牌":
                        this.Medal = "Silver";
                        break;
                    case "铜牌":
                        this.Medal = "Bronze";
                        break;
                }
                this.$router.push({ query: { Medal: this.Medal } });
            },
            "$route.query": function({ opened = false }) {
                console.log(opened);
                if (opened) {
                    this.dialogVisible = true;
                } else {
                    this.dialogVisible = false;
                }
            },
        },
    };
</script>
<style scoped lang="less">
    .description {
        text-align: left;
        text-indent: 2em;
        line-height: 1.3em;
        letter-spacing: 0ch;
        margin: 1em 1em;
        font-size: 18px;
    }
    .centerBox {
        margin: auto auto;
    }
    .cursive {
        font-family: cursive !important;
    }
    .box-card {
        min-height: 300px;
        .el-card__body {
            height: 100%;
            align-items: center;
        }
    }
    .swiper {
        text-align: left;
        display: block;
        overflow-x: hidden;
        font-family: cursive;
        width: 100vw;
        .swiper-slide {
            writing-mode: vertical-rl;
            .text {
                overflow: visible;
                width: 1em;
                cursor: default;
                user-select: none;
                text-overflow: ellipsis;
                font-size: 20px;
                white-space: nowrap;
            }

            .Summer {
                color: #20a736;
            }
            .Winter {
                color: rgb(65, 161, 209);
            }
        }
    }
    .tips {
        border-top: 2px solid #d7d7d75e;
        padding: 1em 0;
    }
    .swiper-slide-active > .text {
        transition: 1s;
        font-weight: bold;
        color: Gold !important;
    }

    .carousel {
        overflow: visible;
        margin: 0 auto;
        width: 800px;
    }
</style>
